// Fonts

@font-face {
  font-family: 'Scala';
  src:
    url('/assets/fonts/ScalaWeb.woff') format('woff');
}

@font-face {
  font-family: 'Scala Italic';
  src:
    url('/assets/fonts/ScalaWeb-Ita.woff') format('woff');
}

// Variables

:root {
  --black: rgba(0, 0, 0, 0.80);
  --dk-grey: rgba(0, 0, 0, 0.8);
  --white: rgba(255, 255, 255, 0.9);
  --pink: rgba(255, 0, 255, 0.5);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

::selection {
  color: white;
  background: rgba(235, 94, 74, 0.75);
}

* {
  margin: 0;
}

body {
  width: 100%;
  height: 100dvh;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

img {
  width: 100%;
  height: auto;
  border-radius: 2px;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  overflow-wrap: break-word;
  font-size: 1rem;
  font-weight: normal;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

strong {
  font-weight: 500;
}

a {
  text-decoration: none;
  transition: all 200ms ease;
  color: #111;
}

img {
  width: 100%;
  height: 100%;
}

// Temp Home
  body#home {
    display: flex;
    width: 100%;
    background: #EBEBEB;
    .container {
      width: 100%;
      height: 100%;
      padding: 5em;
      section {
        display: flex;
        flex-direction: column;
        gap: 1em;
        max-width: 30em;
        p {
          font-family: 'Scala';
          font-size: 1.1rem;
          color: #444;
          em {
            font-family: 'Scala Italic';
          }
        }
      }
    }
  }
